/************************************************************************
BASIC LAYOUT (MOBILE-FIRST)
************************************************************************/
html {
  overflow: scrollbars-vertical;
  overflow-y: scroll; }

html,
body {
  height: 100%; }

header,
main,
nav {
  width: 100%; }

header {
  height: 5%; }

main {
  height: 95%;
  margin: 0 auto; }

nav {
  height: 80%;
  position: fixed;
  bottom: 0; }

main,
nav {
  transition: all 0.5s; }

/************************************************************************
COLLAPSE-NAV CLASSES
************************************************************************/
.mainExtend {
  height: 95%; }

.navCollapse {
  height: 0%; }

/************************************************************************
MEDIA QUERY
************************************************************************/
@media (min-width: 700px) {
  main {
    height: 65%; }

  nav {
    height: 30%;
    bottom: 0%; } }
header {
  background-color: #5b8ba7; }

[id^='content-'] {
  padding: 30px 0; }
  [id^='content-'] p {
    padding: 15px; }

.table {
  display: table; }

.table-cell {
  display: table-cell; }

.content-padding {
  padding-bottom: 15px; }

.text-align-center {
  text-align: center; }

#content-intro,
#content-aboutus,
#content-services,
#content-vehicles,
#content-customers,
#content-contact,
#content-news {
  height: 100%;
  margin: 0 auto; }

#content-intro {
  width: 100%; }

#content-aboutus,
#content-services,
#content-vehicles,
#content-customers,
#content-contact,
#content-news {
  width: 90%; }

#content-intro {
  background-image: url("../img/index-photo.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }

#content-aboutus img {
  width: 40%;
  max-width: 40%;
  border-radius: 100%;
  display: block;
  margin: 0px auto; }

#content-vehicles img {
  width: 80%;
  max-width: 80%;
  border-radius: 7.5px;
  display: block;
  margin: 0px auto; }

#content-customers div {
  text-align: center; }
#content-customers img {
  width: auto;
  max-width: 30%;
  border-radius: 7.5px;
  display: block;
  margin: 0px auto; }

#content-news .postsMenu-mobile {
  display: block;
  margin: 0 auto;
  width: 100%;
  padding: 7.5px;
  font-weight: bold; }
#content-news .postsMenu-desktop {
  display: none;
  padding: 15px; }
  #content-news .postsMenu-desktop::after {
    content: "";
    display: block;
    clear: both; }
  #content-news .postsMenu-desktop li {
    float: left;
    font-size: 24px;
    font-weight: bold;
    color: #fff;
    background-color: #5b8ba7;
    padding: 7.5px 15px;
    margin-right: 7.5px;
    border-radius: 7.5px;
    cursor: pointer;
    transition: all 0.5s; }
    #content-news .postsMenu-desktop li:hover {
      background-color: #FFC784; }
  #content-news .postsMenu-desktop .selected {
    background-color: #FFC784; }
#content-news .post-title {
  font-size: 24px; }

@media (min-width: 700px) {
  .table-cell {
    vertical-align: middle; }

  #content-aboutus,
  #content-services,
  #content-vehicles,
  #content-news {
